import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import { joinClassNames } from 'utils/join-class-names';

import { classNamePrefix } from './_const';

type BombIconProps = Readonly<{
  readonly classNameModifier?: string;
  readonly classNameSuffix: string;
}>;

const BombIcon = ({ classNameModifier, classNameSuffix }: BombIconProps) => (
  <button
    type="button"
    className={joinClassNames(
      classNamePrefix,
      classNameModifier ? `${classNamePrefix}${classNameModifier}` : '',
      classNameSuffix
    )}
  >
    <FontAwesomeIcon icon={['fas', 'bomb']} />
  </button>
);

export default BombIcon;
