import './_mine-field-props.scss';

import { MinesweeperContext } from 'contexts/minesweeper';
import React, { useContext } from 'react';
import { MineFieldPropsVM } from 'view-models';

const MineFieldProps = () => {
  const { mineFieldsProps, mineFieldProps, setMineFieldProps } = useContext(
    MinesweeperContext
  );

  const items = Array.from(mineFieldsProps.values());

  const classNamePrefix = 'MineFieldProps';

  const handleChange = (props: MineFieldPropsVM) => () => {
    setMineFieldProps(props);
  };

  return (
    <form className={classNamePrefix}>
      {items.map((item) => {
        const { uuid, name } = item;

        return (
          <div key={uuid}>
            <input
              type="radio"
              name="mineFieldProp"
              className={`${classNamePrefix}RadioButton`}
              defaultChecked={name === mineFieldProps.name}
              onChange={handleChange(item)}
            />
            &nbsp;
            {name}
          </div>
        );
      })}
    </form>
  );
};

export default MineFieldProps;
