import './_controls.scss';

import { MinesweeperContext } from 'contexts/minesweeper';
import { useDidUpdate } from 'hooks/use-did-update';
import { useInterval } from 'hooks/use-interval';
import React, { useContext, useState } from 'react';

import { formatCount } from './_format-count';
import ResetIcon from './_ResetIcon';

const INTERVAL_DELAY = 1e3;

const Controls = () => {
  const {
    mineFieldStatus: { isSetup, isSolved, isBusted, hiddenMinesCount },
    mineFieldProps,
    reset,
  } = useContext(MinesweeperContext);

  const [secondsCount, setSecondsCount] = useState(0);

  const classNamePrefix = 'Controls';

  const handleResetIconClick = () => {
    setSecondsCount(0);
    reset();
  };

  useInterval(
    () => {
      setSecondsCount(secondsCount + 1);
    },
    isSetup && !(isSolved || isBusted) ? INTERVAL_DELAY : void 0
  );

  useDidUpdate(() => {
    setSecondsCount(0);
  }, mineFieldProps);

  return (
    <div className={classNamePrefix}>
      <div>{formatCount(hiddenMinesCount)}</div>

      <ResetIcon
        isSolved={isSolved}
        isBusted={isBusted}
        classNamePrefix={classNamePrefix}
        onClick={handleResetIconClick}
      />

      <div>{formatCount(secondsCount)}</div>
    </div>
  );
};

export default Controls;
