import './_minesweeper.scss';

import Controls from 'components/Controls';
import Footer from 'components/Footer';
import Header from 'components/Header';
import MineField from 'components/MineField';
import MineFieldProps from 'components/MineFieldProps';
import { defaultMineFieldProps, mineFieldsProps } from 'const';
import { MinesweeperProvider } from 'contexts/minesweeper';
import { useMineField } from 'hooks/use-mine-field';
import React from 'react';

const Minesweeper = () => {
  const mineField = useMineField(defaultMineFieldProps);

  return (
    <MinesweeperProvider value={{ ...mineField, mineFieldsProps }}>
      <Header />

      <main className="Minesweeper">
        <Controls />
        <MineField />
        <MineFieldProps />
      </main>

      <Footer />
    </MinesweeperProvider>
  );
};

export default Minesweeper;
