import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';

type ResetIconProps = Readonly<{
  readonly isSolved: boolean;
  readonly isBusted: boolean;
  readonly classNamePrefix: string;
  readonly onClick: () => void;
}>;

const ResetIcon = ({
  classNamePrefix,
  isSolved,
  isBusted,
  onClick,
}: ResetIconProps) => {
  let icon = <FontAwesomeIcon icon={['far', 'meh']} />;

  if (isSolved) {
    icon = <FontAwesomeIcon icon={['far', 'smile']} />;
  }

  if (isBusted) {
    icon = <FontAwesomeIcon icon={['far', 'frown']} />;
  }

  return (
    /* eslint-disable jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
    <div className={`${classNamePrefix}ResetIcon`} onClick={onClick}>
      {icon}
    </div>
  );
};

export default ResetIcon;
